/**
 * 设备管理
 */
import React, { PureComponent } from 'react';
import DeviceLayout from 'Layouts/DeviceManage';
import {
  Route, Redirect
} from 'react-router-dom';
import {
  DeviceStanding,DeviceWork
} from './configs'

class OrderRouter extends PureComponent {
  render() {
    return <DeviceLayout children={<div>
      <Route exact path="/device" render={() => <Redirect to='/device/standing' />} />
      <Route path='/device/standing' component={DeviceStanding} />
      {/* <Route path='/device/daily' component={DeviceDaily} /> */}
      <Route path='/device/work' component={DeviceWork} />
    </div>} />;
  }

}
export default OrderRouter;